import React, {Component, Fragment} from 'react';
import './BlogPost.css';  
import Post from '../../component/Post/Post'
import axios from 'axios';

class BlogPost extends Component {
	state = {
	 	post: []
	};

	componentDidMount() {
		// ketika component selesai di mounting(buat)
		// fetch('https://jsonplaceholder.typicode.com/posts')
		// .then(response => response.json())
		// .then(json => {
		// 	this.setState({
		// 		post: json
		// 	})
		// })
		axios.get('https://jsonplaceholder.typicode.com/posts')
		.then((res) => {
			this.setState({
				post: res.data
			})	
		})
 	}

	render() {
		return (
			<Fragment>
				<p className="section-title">Blog Post</p>
				{
					this.state.post.map(post => {
						return	<Post key={post.id} title={post.title} desc={post.body}/>
					})
				}
			</Fragment>
 		);
 	}
}

export default BlogPost;

// kita melakukan perulangan untuk merender data dari hasil pemanggil API dengan bantuan fetch. Perulangan data yang di render harus memiliki key(uniqe)(jika menggunakan map).


